<script>
export default {
    name: "Process",
    data() {
        return {
            lang: '',
            screenWidth: 0,
        };
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth()
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth
        },
    },
    mounted() {
        this.updateScreenWidth()
        this.onScreenResize()
