<template>
    <section id="product-form" class="product-form">
        <div class="container">
            <b-form>
                <div class="container-form">
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="name" :label="$t('form.name')" label-for="name">
                                <b-form-input id="name" v-model="name" :placeholder="$t('form.name')"></b-form-input>
                                <div class="error-section" v-if="v$.name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="phone" :label="$t('form.phone')" label-for="phone">
                                <b-form-input id="phone" v-model="phone" :placeholder="$t('form.phone')"></b-form-input>
                                <div class="error-section" v-if="v$.phone.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.phone.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>

                    </div>
                    <div class="row my-row">
                        <div class="col-lg-6">
                            <b-form-group id="company_name" :label="$t('form.company_name')" label-for="company_name">
                                <b-form-input id="company_name" v-model="company_name"
                                    :placeholder="$t('form.company_name')"></b-form-input>
                                <div class="error-section" v-if="v$.company_name.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.company_name.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                        <div class="col-lg-6">
                            <b-form-group id="product_type" :label="$t('form.product_type')" label-for="product_type">
                                <!-- <b-form-input id="product_type" v-model="product_type"
                                    placeholder="product Name"></b-form-input> -->
                                <select v-model="product_type" class="my-select">
                                    <option disabled value="" class="my-option">{{ $t('form.select') }}</option>
                                    <option v-for="(item, index) in product_type_option" :key="index">
                                        {{ item }}
                                    </option>
                                </select>
                                <div class="error-section" v-if="v$.product_type.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.product_type.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div class="row my-row">
                        <div class="col-lg-12">
                            <b-form-group id="message" :label="$t('form.notes')" label-for="message">
                                <b-form-textarea id="message" v-model="message" :placeholder="$t('form.notes_placeholder')"
                                    style="height:120px"></b-form-textarea>
                                <div class="error-section" v-if="v$.message.$error">
                                    <b-icon icon="exclamation-triangle">
                                    </b-icon>
                                    <span class="error">{{ v$.message.$errors[0].$message }}</span>
                                </div>
                            </b-form-group>
                        </div>
                    </div>
                    <div style="text-align:justify">
                        <button v-on:click="submitForm" type="button" class="my-button">
                            <b-spinner v-if="submit && !response" label="Text Centered"></b-spinner>{{ $t('form.submit')
                            }}</button>
                    </div>

                </div>
                <b-modal v-model="modalShow" ok-only style="margin-top: 100px">
                    <p style="text-align: center;margin-top: 15px;font-size: 18px;">
                        {{ $t('success_message') }}
                    </p>
                </b-modal>
            </b-form>
        </div>
    </section>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required, helpers, minLength } from '@vuelidate/validators'

export default {
    name: "productForm",
    setup() {
        return { v$: useVuelidate() }
    },
    components: {

    },
    data() {
        return {
            name: '',
            phone: '',
            company_name: '',
            product_type: '',
            product_type_option: ['Peak Link Academy', 'Peak Link Clinic', 'Peak Link ERP', 'Peak Link HR'],
            message: '',
            modalShow: false,
            response: false,
            submit: false,
        };
    },
    validations() {
        return {
            name: {
                required: helpers.withMessage('This field is required', required)
            },
            phone: {
                required: helpers.withMessage('This field is required', required),
                minLength: helpers.withMessage('Please enter valid phone number', minLength(10))
            },
            company_name: {
                required: helpers.withMessage('This field is required', required)
            },
            product_type: {
                required: helpers.withMessage('This field is required', required)
            },
            message: {
                required: helpers.withMessage('This field is required', required)
            }

        }
    },
    methods: {
        submitForm() {
            this.v$.$validate();
            if (!this.v$.$error) {
                this.submit = true
                this.sendData()
            }
        },
        sendData() {
            const formData = new FormData()
            formData.append('client_name', this.name)
            formData.append('phone', this.phone)
            formData.append('company_name', this.company_name)
            formData.append('Project_name', this.product_type)
            formData.append('message', this.message)
            console.log(formData)
            for (var pair of formData.entries()) {
                console.log(pair)
            }
            this.axios.post("https://peaklinksite.peaklinkdemo.com/api/order_demo", formData)
                .then((res) => {
                    console.log(res)
                    this.response = true
                    if (res.status == 200)
                        this.modalShow = true
                })
        }
    }
};
</script>

<style lang="scss">
.product-form {
    padding: 50px
}

.product-form .form-group {
    justify-content: space-between;
    margin: 20px 0px;
}

.product-form .container-form {
    margin-right: 4%;
    margin-left: 4%;
    margin-top: 30px;
}

.product-form .form-control,
.product-form .custom-file {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 10px;
    background-color: #bebebe11;
    transition: all 0.5s ease;
}

.product-form .form-control:focus {
    box-shadow: none;
    border: 1px solid var(--main-blue);
    background: #fff;
    transition: all 0.5s ease;
    box-shadow: 0px 0px 6px #24487961;
}

.product-form .d-block {
    color: var(--main-blue);
    text-align: justify;
    font-size: 18px;
}



@media (min-width: 992px) {
    .product-form .my-row {
        margin-top: 20px;
    }
}

.product-form .error {
    color: #b61532;
    margin: 5px;
}

.product-form .bi-exclamation-triangle {
    color: #b61532;
    font-size: 18px;
}

.product-form .error-section {
    margin-top: 10px;
    text-align: justify;
}




//button
.product-form .my-button {
    position: relative;
    color: var(--main-blue);
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    margin: 30px 0 0;
    border: 1px solid var(--main-blue);
    padding: 10px 30px;
    overflow: hidden;
    transition: .6s all ease;
    background: transparent;
    border-radius: 10px;
    z-index: 100000;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 55px;
}

.product-form .my-button::before {
    background: var(--main-blue);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: all .6s ease;
}

.product-form .my-button .spinner-border {
    width: 23px !important;
    height: 23px !important;
    margin: 0px 10px 0px 0px;
    color: var(--main-blue)
}
html:lang(en) .product-form .my-button .spinner-border {
    margin: 0px 10px 0px 0px;
}
html:lang(ar) .product-form .my-button .spinner-border {
    margin: 0px 0px 0px 10px;
}

.product-form .my-button:hover {
    color: #fff;
    font-weight: 500;

    .spinner-border {
        color: #fff !important
    }
}

.product-form .my-button::before {
    width: 0%;
    height: 102%;
}

.product-form .my-button:hover::before {
    width: 102%;
}


.product-form .my-select {
    outline: none;
    border: 1px solid rgb(230, 230, 230);
    border-radius: 4px;
    height: 50px;
    margin-top: 2px;
    background-color: rgba(190, 190, 190, 0.0666666667);
    transition: all 0.5s ease;
    width: 100%;
    padding: 0px 10px;
    color: #495057;
}</style>