import Vue from 'vue'
import VueRouter from 'vue-router'
import Landing from '@/views/Landing.vue'
import GetProjectForm from "@/views/GetProjectForm.vue"
import GetJobForm from "@/views/GetJobForm.vue"
import OrderProductForm from "@/views/OrderProductForm.vue"
import Portfolio from "@/views/Portfolio.vue"
import AboutPage from "@/views/AboutPage.vue"
import Project from "@/views/Project.vue"
import ProductTypePage from '@/views/ProductTypePage.vue'
import PartnerPage from "@/views/PartnerPage.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'landing',
    component: Landing
  },
  {
    path: '/get-project',
    name: 'get-project',
    component: GetProjectForm
  },
  {
    path: '/get-job',
    name: 'get-job',
    component: GetJobForm
  },
  {
    path: '/order-product',
    name: 'order-product',
    component: OrderProductForm
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: Portfolio
  },
  {
    path: '/about',
    name: 'about',
    component: AboutPage
  },
  {
    path: '/project',
    name: 'project',
    component: Project
  },
  {
    path: '/product-type',
    name: 'product-type',
    component: ProductTypePage
  },
  {
    path: '/partner',
    name: 'partner',
    component: PartnerPage
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
